lizan para llevar a cabo este proceso, con ventajas como la reutilización de compuestos absorbidos, pero con la desventaja de generar residuos sólidos. Esta técnica es eficaz para eliminar una variedad de contaminantes gaseosos en aplicaciones industriales y ambientales.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img14.jpg"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Absorción"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Es un proceso en el que un gas contaminante se disuelve en un líquido, generalmente agua. Es una técnica eficaz y de bajo costo para eliminar contaminantes gaseosos, pero no funciona para todos los compuestos. Se utiliza en diversas aplicaciones industriales y ambientales.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img15.jpg"),"alt":"Imagen decorativa"}})])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Condensación"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Es un proceso en el que un gas contaminante se enfría, transformándose en un líquido, lo que permite la eliminación de los contaminantes del gas. Se utilizan condensadores de contacto, donde el gas se pone en contacto con un líquido frío, o condensadores de superficie, donde el gas fluye sobre una superficie fría con líquido refrigerante. Esta técnica es eficaz para la eliminación de contaminantes gaseosos que se condensan a temperaturas específicas y es utilizada en diversas aplicaciones industriales.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img16.jpg"),"alt":"Imagen decorativa"}})])])])]),_c('Separador'),_vm._m(23),_vm._m(24),_vm._m(25),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-6"},[_c('LineaTiempoD',{staticClass:"color-primario mb-4"},